.content{background-color:#fff6f6;padding:50px 0}.content h1{color:#333;text-align:center;margin-bottom:4px}.content hr{width:40px;height:2px;background:#6a00a1;border:none;border-radius:12px;margin:0 auto 50px}.content .graduateContent{display:flex;align-items:flex-start;justify-content:space-between;gap:40px}.content .graduateContent .title{flex:1;font-family:"Roboto",sans-serif;color:#333}.content .graduateContent .title p{font-size:1.5rem;line-height:1.5;margin-bottom:20px}.content .graduateContent .title span{font-style:italic;display:block;font-size:.9rem;font-weight:300;color:#6a6a6a;margin-bottom:40px}.content .graduateContent .wrapper{display:flex;justify-content:flex-start;gap:40px}.content .graduateContent .wrapper svg{cursor:pointer;font-size:1.5rem;color:#555;transition:color .3s ease}.content .graduateContent .wrapper svg:hover{color:#000}.content .graduateContent .image{flex-shrink:0;width:340px;height:400px;box-shadow:0px 4px 15px rgba(0,0,0,.1);overflow:hidden;border-radius:8px}.content .graduateContent .image img{width:100%;box-shadow:0px 4px 15px rgba(0,0,0,.1);object-fit:cover;height:100%}@media(max-width: 884px){.content .graduateContent{flex-direction:column-reverse;align-items:center}.content .graduateContent .title{text-align:center}.content .graduateContent .title p{font-size:1.2rem}.content .graduateContent .wrapper{justify-content:center;margin-top:20px}.content .graduateContent .image{width:100%;max-width:340px;height:auto;aspect-ratio:1/1}.content .graduateContent .image img{width:100%;height:100%}}@media(max-width: 430px){.content{padding:30px 0}.content .graduateContent .title p{font-size:1rem}.content .graduateContent .image{max-width:280px}.content .graduateContent .image img{width:100%;height:100%}}